@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: bisque;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gameDesk {
  margin: 50px auto;
  width: 470px;
  height: 470px;

  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 25px;
}

.gameDesk-grid {
  width: 143px;
  height: 143px;

  margin-top: 10px;
  margin-left: 10px;

  background: #ffffff;
  border: 3px solid #52acff;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  float: left;
  box-sizing: border-box;
}

.settings {
  /* background-color: blue; */
  margin: 50px auto;
  width: 470px;
}

.newGameClass {
  width: 470px;
  align-items: center;
}

.soundSettings {
  /* margin: 50px auto; */
  width: 470px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}



.PlayerMarker img{
  margin-top: 10px;
  margin-left: 10px;
}


.blink6 {
  animation: blink6 0.1s  1;

}
@keyframes blink6 {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.score {
  margin: 50px auto;
  width: 462px;
  height: 112px;

  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 130%;
  /* or 125px */


  align-items: center;
  text-align: center;
  letter-spacing: 0.06em;

  color: #ffffff;
  /* background-color: blueviolet; */
  text-align: center;
}

.player1{
  color: #ff5454;
}

.player2{
  color: #5BCEFF;
}

